import { InjectionKey } from "vue";
import { createStore, Store, useStore as vuexUseStore } from "vuex";
import http from "@/html";
import { IComments } from "@/interfaces/IComments";
import { IUser } from "@/interfaces/IUser";
import { IReplies } from "@/interfaces/IReplies";

let contadorIdComment = 2
let contadorIdReply = 4

interface Estado {
    Users: IUser[]
    Comments: IComments[]
}

export const key : InjectionKey<Store<Estado>> = Symbol()

export const store = createStore<Estado>({
    state:{
        Users:[
             {
                 
                 image: { 
                     png: "../images/avatars/image-juliusomo.png",
                     webp: "data:image/webp;base64,"
                 },
                 username: "juliusomo"
                  
             }
        ],
        Comments:[
             {
                 id: 1,
                 content: "Impressive! Though it seems the drag feature could be improved. But overall it looks incredible. You've nailed the design and the responsiveness at various breakpoints works really well.",
                 createdAt: "1 month ago",
                 score: 12,
                 user: {
                 image: { 
                 png: "../images/avatars/image-amyrobson.png",
                 webp: "data:image/webp;base64,"
                 },
                 username: "amyrobson"
                 },    
                 replies: []
               
         },
         {
         id: 2,
         content: "Woah, your project looks awesome! How long have you been coding for? I'm still new, but think I want to dive into React as well soon. Perhaps you can give me an insight on where I can learn React? Thanks!",
         createdAt: "2 weeks ago",
         score: 5,
         user: {
           image: { 
             png: "../images/avatars/image-maxblagun.png",
             webp: "data:image/webp;base64,"
           },
           username: "maxblagun"
         },
         replies: [
            {
                id: 3,
                content: "If you're still new, I'd recommend focusing on the fundamentals of HTML, CSS, and JS before considering React. It's very tempting to jump ahead but lay a solid foundation first.",
                createdAt: "1 week ago",
                score: 4,
                replyingTo: "maxblagun",
                user: {
                  image: { 
                    png: "../images/avatars/image-ramsesmiron.png",
                    webp: "data:image/webp;base64,"
                  },
                  username: "ramsesmiron"
                }
              },
              {
                id: 4,
                content: "I couldn't agree more with this. Everything moves so fast and it always seems like everyone knows the newest library/framework. But the fundamentals are what stay constant.",
                createdAt: "2 days ago",
                score: 2,
                replyingTo: "ramsesmiron",
                user: {
                  image: { 
                    png: "../images/avatars/image-juliusomo.png",
                    webp: "data:image/webp;base64,"
                  },
                  username: "juliusomo"
                }
              }
         ]
        }
        ]
        
    },
    mutations:{
         'SET_COMMENTS'(state,comments: IComments[]){
              state.Comments = comments
          },
          'SET_USERS'(state,users: IUser[]){
            state.Users = users
            console.log(state.Users)
          },  
         'ADD_COMMENT'(state,content:string){
            const createdAt = new Date().toISOString().substr(11,8)
            contadorIdComment++
            const id = contadorIdComment
            console.log(contadorIdComment)
            const comment = {
                id: id,
                content: content,
                createdAt: createdAt,
                score: 0,
                user: state.Users[0],
                replies: []
            } as IComments
            state.Comments.push(comment)
         },
          'REMOVE_COMMENT'(state,id :number){
              state.Comments = state.Comments.filter(comnt => comnt.id != id )
          },
          'EDIT_COMMENT'(state,Comment:IComments){
              const index = state.Comments.findIndex(comnt => comnt.id == Comment.id)
              state.Comments[index].content = Comment.content
          },
          'ADD_REPLY'(state,Reply:IReplies){
             const createdAt = new Date().toISOString().substr(11,8)
             contadorIdReply++
             const id = contadorIdReply
             console.log(contadorIdReply)
             const index = state.Comments.findIndex(comnt => comnt.id == Reply.id)
             if(Reply.replyingTo === ''){
                Reply.replyingTo = state.Comments[index].user.username
             }
             const replies = {
                 id: id,
                 content: Reply.content,
                 createdAt: createdAt,
                 score: 0,
                 user: state.Users[0],
                 replyingTo: Reply.replyingTo
             } as IReplies
             state.Comments[index].replies.push(replies)
            
          },
          'REMOVE_REPLY'(state,ids : any){
                const index = state.Comments.findIndex(comnt => comnt.id == ids[0])

                state.Comments[index].replies = state.Comments[index].replies.filter(comnt => comnt.id != ids[1])

          },    
          'EDIT_REPLY'(state,info :any){
            const indexC = state.Comments.findIndex(comnt => comnt.id == info[0])
            const indexR = state.Comments[indexC].replies.findIndex(resp => resp.id == info[1])
            state.Comments[indexC].replies[indexR].content = info[2]
          }
          
    },
    actions:{
        //  'GET_COMMENTS'({commit}){
        //      http.get('comments')
        //      .then(resp => commit('SET_COMMENTS',resp.data))
        //  },
         'GET_USERS'({commit}){
            http.get('currentUser')
            .then(resp => console.log(resp.data))
        },
   }
    
})

export function useStore() : Store<Estado>{
    return vuexUseStore(key)
}